import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Scrollbar } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/scrollbar";
import Link from "next/link";

export default function EventCarousel({ events }) {
  return (
    <div className="mt-12">
      <h2 className="text-3xl font-bold mb-4">ðŸ”¥ Trending Events</h2>

      <Swiper
        modules={[Navigation, Scrollbar]}
        spaceBetween={20}
        slidesPerView={3}
        navigation
        scrollbar={{ draggable: true }}
        breakpoints={{
          320: { slidesPerView: 1 },
          640: { slidesPerView: 2 },
          1024: { slidesPerView: 3 },
        }}
      >
        {events.map((event) => (
          <SwiperSlide key={event.id}>
            <Link href={`/events/${event.id}`}>
              <div className="bg-gray-800 rounded-xl p-4 hover:scale-[1.02] transition shadow-lg cursor-pointer">
                <img
                  src={event.images?.[0]?.url}
                  className="h-48 w-full object-cover rounded-lg"
                />

                <h3 className="text-xl font-semibold mt-3">{event.name}</h3>
                <p className="text-gray-400 text-sm">
                  {event.dates?.start?.localDate}
                </p>

                <p className="text-green-400 mt-2 font-semibold">
                  From â‚¹{Math.floor(Math.random() * (4500 - 900) + 900)}
                </p>
              </div>
            </Link>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}
